import React, {useEffect, useState} from 'react';
import {userStore} from "../store/uiStore";
import {useMiningStore} from "../store/miningStore";
import {CtaButton} from "../styles/styles";
import MiningCoinIcon from '../assets/images/mining-coin-icon.svg';
import styled from "styled-components";
import CoinIconBlack from "../assets/images/coin-icon-black.svg";
import TimerIcon from "../assets/images/timer-icon.svg";
import ButtonFigure from "../assets/images/buttonFigure.svg";
import Popup from "./Popup";
import {claimMining, startMining} from "../api/api";


const MiningButton: React.FC = () => {
    const [isTimeUp, setIsTimeUp] = useState(false);
    const [isPopupVisible, setPopupVisible] = useState(false);
    const [miningReward, setMiningReward] = useState(0);

    const increaseBalance = userStore((state) => state.increaseBalance)
    const currentRank = userStore((state) => state.currentRank)

    const timeLeft = useMiningStore((state) => state.timeLeft)
    const isMining = useMiningStore((state) => state.isMining)
    const startMiningStore = useMiningStore((state) => state.startMiningStore)
    const nextMiningDate = useMiningStore((state) => state.nextMiningDate)
    const stopMining = useMiningStore((state) => state.stopMining)


    useEffect(() => {

        if (timeLeft === 0) {
            setIsTimeUp(true);
        }
    }, [timeLeft, isMining]);


    const formatTime = (time: number) => {
        const hours = Math.floor(time / 3600);
        const minutes = Math.floor((time % 3600) / 60);
        const seconds = time % 60;
        return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
    };

    const handleStartMining = async () => {
        const miningResponse = await startMining()
        console.log('nextMiningDate from handleStartMining', nextMiningDate)
        startMiningStore(nextMiningDate)
    };
    const handleClaimReward = async () => {
        if (timeLeft <= 0 && isMining) {

            try {
                const miningResponse = await claimMining()
                console.log('miningResponse', miningResponse)

                increaseBalance(miningResponse.data.max_extraction)
                setMiningReward(miningResponse.data.max_extraction)
                setIsTimeUp(false)
                setPopupVisible(true);
                stopMining()
            } catch (error) {
                console.log(error)
            }

        }
    };


    return (
        <MiningButtonContainer disabled={currentRank < 2} >
            {isMining ? (
                <ButtonContent>
                    <Wrapper>
                        <Icon src={CoinIconBlack} alt="Coin Icon"/>
                        <ButtonPrice>10000</ButtonPrice>
                    </Wrapper>
                    <CentralIcon src={ButtonFigure} alt=""/>
                    {isTimeUp ? (
                        <ButtonTitle onClick={handleClaimReward}>
                            Claim
                        </ButtonTitle>

                    ) : (
                        <TimerWrapper>
                            <Icon src={TimerIcon} alt="Coin Icon"/>
                            <div>{formatTime(timeLeft)}</div>
                        </TimerWrapper>

                    )}

                </ButtonContent>
            ) : (
                <StartMiningButton onClick={handleStartMining} disabled={!(currentRank > 1)}>
                    <MiningCoin src={MiningCoinIcon} alt="Mining Coin Icon"/>
                    Start Mining
                </StartMiningButton>
            )}
            {isPopupVisible && <Popup
                onClose={() => setPopupVisible(false)}
                rewards={[{id: 0, type: "referral", amount: miningReward, inspirations: 0, replenishments: 0}]}
            />}

        </MiningButtonContainer>
    );
};

const MiningButtonContainer = styled(CtaButton)`
    display: flex;
    justify-content: center;
`;


const MiningCoin = styled.img`
    margin-right: 5px;
`;
const StartMiningButton = styled.button`
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    align-items: center;
    border: none;
    outline: none;
    border-radius: inherit;
    text-transform: uppercase;
    color: black;
`;

const ButtonContent = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0 15px;
`;

const TimerWrapper = styled.div`
    display: flex;
    align-items: center;
    width: 50%;
    justify-content: flex-end;

`;
const Wrapper = styled.div`
    display: flex;
    font-size: 14px;
    width: 50%;
    align-items: center;
`;
const ButtonPrice = styled.div`
`;
const ButtonTitle = styled.div`
    display: flex;
    font-size: 14px;
    height: 100%;
    margin-left: auto;
    align-items: center;
    width: 50%;
    justify-content: end;

`;
const Icon = styled.img`
    margin-right: 10px;
`;
const CentralIcon = styled.img`
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    @media (max-height: 750px) {
        height: 4vh;
    }
`;

export default MiningButton;
