import {userStore} from "../store/uiStore";
import {useMemo} from "react";

export const useTapPower =  () => {
    // const {  isInspirationBoostActive, pressForce } = userStore(state => ({
    //     pressForce: state.pressForce,
    //     isInspirationBoostActive: state.isInspirationBoostActive,
    //
    // }));

    const isInspirationBoostActive = userStore(state => state.isInspirationBoostActive)
    const pressForce = userStore(state => state.pressForce)

    // Мемоизируем значение на основе зависимостей
    const tapPower = useMemo(() => {
        const basePressForce = pressForce;
        return isInspirationBoostActive ? basePressForce * 3 : basePressForce;
    }, [isInspirationBoostActive, pressForce]);
    return tapPower;
};
