import {create} from 'zustand';
import {getLeaderboard, getTasks} from "../api/api";
import {toast} from "react-toastify";
import {Task} from "./tasksStore";



interface LeaderboardUser {
    rank: number;
    username: string;
    id: number;
    coins: number
    isCurrentUser: boolean
}

interface Season {
    id: string;
    startDate: string;
    endDate: string;
}

interface LeaderboardState {
    seasons: Season[];
    currentSeasonIndex: number;
    leaderboard: LeaderboardUser[];
    currentUser: LeaderboardUser | null;
    setCurrentSeason: (index: number) => void;
    setCurrentUser: (user: LeaderboardUser) => void;
    loading: boolean,
    error: string | null,
    fetchLeaderboard: () => void,
}

export const useLeaderboardStore = create<LeaderboardState>((set) => ({
    seasons: [
        {id: '1', startDate: '01 Jul. 2024', endDate: '01 Aug. 2024'},
        {id: '2', startDate: '01 Aug. 2024', endDate: '01 Sep. 2024'},
    ],
    currentSeasonIndex: 0,
    leaderboard: [],
    currentUser: null,
    setCurrentSeason: (index) => set({currentSeasonIndex: index}),
    fetchLeaderboard: async () => {
        set({ loading: true, error: null });
        try {
            const leaderboard: LeaderboardUser[] = await getLeaderboard() // Ваш API для получения задач
            console.log('leaderboardleaderboardleaderboard', leaderboard)
            set({ leaderboard, loading: false });
        } catch (error) {
            set({ error: 'Failed to load tasks', loading: false });
            toast.error('Failed to load tasks');
        }
    },
    setCurrentUser: (user) => set({currentUser: user}),
    loading: false,
    error: null,
}));
