// hooks/useRewardPopup.ts
import { useState, useEffect } from 'react';
import { claimRewards, getQuestionStatus } from '../api/api';
import { useRewardStore } from '../store/rewardStore';
import {updateUiWithTelegramData} from "../services/uiSyncService"; // предположим, что вы используете Zustand или другой стор для хранения наград

export const useRewardPopup = () => {
    const [isRewardPopupVisible, setIsRewardPopupVisible] = useState(false);
    const [popupRewards, setPopupRewards] = useState<any[]>([]); // награды из стора
    const rewards = useRewardStore((state) => state.rewards); // получаем награды из стора

    useEffect(() => {
        if (rewards.length > 0) {
            setPopupRewards(rewards); // сохраняем награды для отображения в попапе
            setIsRewardPopupVisible(true); // показываем попап
        }
    }, [rewards]); // следим за изменениями в наградах

    const handleClosePopup = async () => {
        await claimRewards(); // Забираем награды
        await getQuestionStatus(); // Обновляем статус вопроса
        setIsRewardPopupVisible(false); // Закрываем попап
        updateUiWithTelegramData()
    };

    return {
        isRewardPopupVisible,
        popupRewards,
        handleClosePopup
    };
};
