import {userStore} from "../store/uiStore";
import {initDataStore} from "../store/initDataStore";
import {getUserProfile} from "../api/api";

export const updateUiWithTelegramData = async () => {

    if (window.Telegram && window.Telegram.WebApp) {
        window.Telegram.WebApp.disableVerticalSwipes();

        const initData = window.Telegram.WebApp.initData;
        const telegramUser = window.Telegram.WebApp.initDataUnsafe.user;

        if (initData) {
            console.log("initData", initData);
            initDataStore.getState().setIntiData(initData);
    try {
        const responseData = await getUserProfile()
        console.log(responseData)
        userStore.getState().updateUiUserData(responseData);

    } catch (error) {
        console.log(error);
    }

    } else {
        console.error('initData is not available.');
    }
    } else {
        console.error('Telegram WebApp is not available.');
    }
}