import React from 'react';
import ConfettiAnimation from './ConfettiAnimation';
import styled from 'styled-components';
import {colors, CtaButton} from '../styles/styles';
import {Reward} from "../store/rewardStore";
import inspirationBoostIcon from "../assets/images/inspiration-boost-icon.svg"
import rebirthBoostIcon from "../assets/images/rebirth-boost.svg"
import coinIcon from "../assets/images/coin-icon-green.svg"

interface PopupProps {
    onClose: () => void;
    header?: string; // Заголовок (опциональный)
    subTitle?: string; // Подзаголовок (опциональный)
    rewards?: Reward[]; // Значение награды (опциональное)
}

const Popup: React.FC<PopupProps> = ({
                                         onClose,
                                         header = "Congratulation!!", // Значение по умолчанию
                                         subTitle = "You've earned!", // Значение по умолчанию
                                         rewards = [], // Значение по умолчанию
                                     }) => {

    let map = {
        inspirations: 0,
        replenishments: 0,
        amount: 0

    }
    for (const item of rewards) {
        map.inspirations += item.inspirations;
        map.amount += item.amount;
        map.replenishments += item.replenishments;
    }

    return (
        <PopupWrapper className="reward-popup">
            <ConfettiAnimation/>
            <PopupHeader>{header}</PopupHeader>
            <PopupSubTitle>{subTitle}</PopupSubTitle>
            {rewards.length > 0 && (
                <RewardItem>
                    {map.inspirations > 0 && (
                        <RewardTitle>

                            <Icon src={inspirationBoostIcon}/>
                            Inspiration boost {map.inspirations}
                        </RewardTitle>

                    )}

                    {map.replenishments > 0 && (
                        <RewardTitle>
                            <Icon src={rebirthBoostIcon}/>
                            replenishments {map.replenishments}
                        </RewardTitle>
                    )}


                    {map.amount > 0 && (
                        <RewardTitle>
                            <Icon src={coinIcon}/>
                            amount {map.amount}
                        </RewardTitle>
                    )}


                </RewardItem>
            )}
            <ClosePopupButton onClick={onClose}>OK</ClosePopupButton>
        </PopupWrapper>
    );
};

const PopupWrapper = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: calc(100% - 20px);
    border: 1px solid #69FF93;
    align-items: center;
    position: fixed;
    bottom: 8.5vh;
    left: 50%;
    height: 86%;
    transform: translate(-50%, 0);
    background: white;
    padding: 20px;
    border-radius: 8px;
    z-index: 1000;
    background: radial-gradient(163.59% 90.02% at 50% 0%, rgba(68, 94, 76, 0.8) 0%, rgba(9, 9, 17, 0.8) 77.42%);
    box-shadow: inset 0px 0px 130px #090911;
    backdrop-filter: blur(4px);

`;

const PopupHeader = styled.h1`
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    text-transform: uppercase;
    color: #FFFFFF;
    z-index: 10001;
    margin-bottom: 15px;
`;

const PopupSubTitle = styled.h2`
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: #A7A7A7;
    margin-bottom: 50px;
`;

const RewardTitle = styled.h2`
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-bottom: 2vh;
`;

const Icon = styled.img`
    margin-right: 1vh;
`;
const RewardItem = styled.div`
    display: flex;
    flex-direction: column;
`;
const ClosePopupButton = styled(CtaButton)`
    width: 100px;
    height: 32px;
`;

export default Popup;
