import { create } from 'zustand';
import { toast } from 'react-toastify';
import {completeTask, getTasks, startTaskApi} from "../api/api";
import {useRewardStore} from "./rewardStore";

interface TaskStore {
    tasks: Task[];
    loading: boolean;
    error: string | null;
    fetchTasks: () => Promise<void>;
    completeTask: (id: number) => void;
    startTask: (id: number) => void;
    claimReward: (taskId: number) => void;
    setTasks: (tasks: Task[]) => void;
    checkStatus: () => void;
}

export interface Task {
    id: number;
    status: 'not_started' | 'pending' | 'in_progress' | 'completed' | 'claimed';
    reward: Reward;
    description: string;
    condition: {
        type: string;
        visit_link_condition: {
            url: string;
        }
    }
    user_tasks: {
        id: number;
        completed_time: string
        create_time: string
        user: {
            id: number
        }
    }[]
}

interface Reward {
    id: number;
    tokens: number;
    inspirations: number;
    replenishments: number;
}

export const useTaskStore = create<TaskStore>((set, get) => ({
    tasks: [],
    loading: false,
    error: null,

    // Асинхронная функция для загрузки данных
    fetchTasks: async () => {
        set({ loading: true, error: null });
        try {
            const tasks: Task[] = await getTasks(); // Ваш API для получения задач

            // Добавляем статус к каждой задаче
            const tasksWithStatus = tasks.map((task: Task) => {
                // Инициализируем статус как 'not_started'
                let status: Task['status'] = 'not_started';

                // Проверка наличия user_tasks и определение статуса задачи
                if (task.user_tasks && task.user_tasks.length > 0) {
                    const userTask = task.user_tasks.find(item => task.id === item.id);

                    if (userTask) {
                        if (userTask.completed_time) {
                            // Если есть completed_time, статус - 'completed'
                            status = 'completed';
                        } else if (userTask.create_time && !userTask.completed_time) {
                            // Если есть create_time, но нет completed_time, статус - 'pending'
                            status = 'pending';
                        }
                    }
                }

                return {
                    ...task,
                    status,
                };
            });

            set({ tasks: tasksWithStatus, loading: false });
        } catch (error) {
            set({ error: 'Failed to load tasks', loading: false });
            toast.error('Failed to load tasks');
        }
    },


    checkStatus: () => {


    },

    startTask: (taskId: number) => {
        const task = get().tasks.find((task) => task.id === taskId);
        if (task && task.status === 'not_started') {
            set((state) => ({
                tasks: state.tasks.map((t) =>
                    t.id === taskId ? { ...t, status: 'pending' } : t
                ),
            }));

            // Если задача имеет тип 'visit_link', открываем ссылку
            if (task.condition?.type === 'visit_link' && task.condition.visit_link_condition?.url) {
                window.open(task.condition.visit_link_condition.url, '_blank');
            }


            // Имитируем выполнение задачи, через 10 секунд статус станет "completed"
            setTimeout(() => {
                set((state) => ({
                    tasks: state.tasks.map((t) =>
                        t.id === taskId ? { ...t, status: 'completed' } : t
                    ),
                }));
                completeTask(taskId)

            }, 10000); // Имитируем выполнение задачи через 10 секунд

            startTaskApi(taskId)

        }
    },

    completeTask: (taskId: number) => {
        set((state) => ({
            tasks: state.tasks.map((task) =>
                task.id === taskId ? { ...task, status: 'completed' } : task
            ),
        }));
    },

    claimReward: (taskId: number) => {
        set((state) => ({
            tasks: state.tasks.map((task) =>
                task.id === taskId ? { ...task, status: 'claimed' } : task
            ),
        }));
    },

    setTasks: (tasks: Task[]) => set({ tasks }),
}));
