import axios from 'axios';
import {initDataStore} from "../store/initDataStore";
import {toast} from "react-toastify";

// const API_URL = 'http://localhost:5577/proxy';
//
//
const proxyUrl = "https://cors-anywhere.herokuapp.com/";
const targetUrl = "http://147.45.232.159:180/api";
const API_URL = proxyUrl + targetUrl;

const getHeaders = () => {
    const initData = initDataStore.getState().initData;
    return {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'X-Telegram-Init-Data': initData,
    }
}

export const getUserProfile = async () => {
    // Получаем актуальное состояние из хранилища
    const headers = getHeaders()


    try {
        const response = await axios.get(`${API_URL}/user/profile`, {headers: headers});

        const responseData = response.data.body
            ? JSON.parse(response.data.body).data
            : response.data.data;
        return responseData
    } catch (error) {
        console.error('Ошибка при getQuestionStatus:', error);
        toast.error(`${error}`, {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
        });
    }
};

export const syncClicks = async (clicks: number) => {
    // Получаем актуальное состояние из хранилища
    const headers = getHeaders()


    try {
        const response = await axios.patch(`${API_URL}/game_actions/sync_clicks`,
            {clicks},
            {headers: headers}
        );

        console.log('Успешная синхронизация:', response.data);
    } catch (error) {
        console.error('Ошибка при синхронизации:', error);
        toast.error(`${error}`, {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
        });
    }
};

export const startMining = async () => {
    const headers = getHeaders()

    try {
        // Важно: передача данных в POST должна быть в первом аргументе, а заголовки во втором.
        const response = await axios.post(`${API_URL}/mining/start`, {}, {headers});
        console.log('Успешный старт майнинга:', response.data);
        return response.data;
    } catch (error: any) {
        console.error('Ошибка старта майнинга:', error);
        toast.error(error.response?.data?.message.text || 'Ошибка старта майнинга', {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
        });
    }
};

