export const getRewardsList = async (): Promise<any | undefined> => {
    // Получаем актуальное состояние из хранилища
    const headers = getHeaders()

    try {
        const response = await axios.get(`${API_URL}/reward/list`,
            {headers: headers}
        );

        if (response.data.status_code === 404) {
            console.log('da 404')
            return
        }

        if (response.data.message.status === 'Error') {
            toast.error(`${response.data.message.text}`, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "dark",
            });
            return
        }

        const responseData = response.data.body
            ? JSON.parse(response.data.body).data.rewards
            : response.data.data.rewards;

        console.log('Успешный fetchRewards:', response.data);
        return responseData
    } catch (responseError) {

    }
};

