export const sendQuestion = async (question: string) => {
    const headers = getHeaders(); // Функция получения заголовков
    console.log(headers);

    try {
        // Корректно передаем заголовки третьим аргументом
        const response = await axios.post(
            `${API_URL}/questions/ask?question=${encodeURIComponent(question)}`,
            {},
            {headers} // Заголовки передаем как опции запроса
        );

        console.log('Успешный get qst status:', response.data);
        return response.data;
    } catch (error: any) {
        console.error('Ошибка при синхронизации:', error.response?.data || error.message);
        toast.error(`Ошибка: ${error.response?.data?.detail || error.message}`, {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
        });
    }
};

